*{
  padding: 0px;
  margin: 0px;
}

.header{
  
  margin-top: 3rem;
  display: flex;
  align-items: center;
  gap: 9px;
  justify-content: center;
  font-size: 20px;
}
.header2{
text-align: center;
font-size: 15px;
padding: 0px;
margin: 5px;
text-transform: capitalize;
}
.ownerspace{
  display: flex;
  justify-content: center;
}
.ownerimage{
  border-radius: 1rem;
}
.speechlogo{
font-size: larger;
 color:rgb(75, 144, 217);
}
.name{
  font-size: medium;
  font-weight: bolder;
  padding: 2px;

}

@media (min-width:200px) and (max-width:500px) {
  .header{
    margin-top: 1rem;
  }


  .header2{
    font-size: 12px;
  }
}

.left-foot-section{
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 0px;
}
.footer{

  background: skyblue;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  gap: 4px;
}



.footer2 {
  padding: 20px;
  text-align: center;
}

.footer-icons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-icon {
  font-size: 24px; /* Adjust the icon size as needed */
  color: #080808; /* Set the icon color */
  margin: 10px; /* Adjust the spacing between icons */
  text-decoration: none; /* Remove underlines from links */
}

.footer-icon:hover {
  color: #e5e9ea; /* Change the color on hover */
}
