.speech2text {
  color: rgb(18, 44, 122);
  font-weight: bold;
  outline: none;
  font-size: large;
}
.loadtext{
  text-align: center;
}

.app {
  overflow: hidden;

  min-height: 300px;
  width: 700px;
  border-radius: 15px 0px 15px 15px;
  padding: 0px 0rem 1rem 1rem;
  margin: 20px auto 20px auto;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.speech2textbox {
  overflow: scroll;
  overflow-y: hidden;
  overflow-x: hidden;
  min-height: 190px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
  rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  padding: 1rem;
  margin: 1rem;
}

.headerbtn {
  display: flex;
  justify-content: end;
}
.copybutton {
  display: flex;
  align-items: center;
  gap: 3px;
  padding: 0.7rem;
  transition: 0.3s ease-in-out;
  opacity: 0.9;
  border-radius: 0px 0px 0px 3px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.copybutton:hover {
  opacity: 1;
  cursor: pointer;
}

.copybuttonouter {
  transition: 0.3s;
}
.copybuttonouter:hover {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}

.miclogo {
  font-size: 25px;
 transition: .3s;
  padding: 0.6rem;
  border-radius: 50%;
  cursor: pointer;box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.micstart{
    color:rgb(75, 144, 217);
    border: 1px solid blue;
}
.micstop{
    color: red;
    border: 1px solid red;
    
}

.miclogo:hover{
transform: scale(1.03);
box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
.buttonbox {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 9px;
}
.lootieanimation{
  width: 100px;
}

@media (min-width:200px) and (max-width:500px) {
  .app{
    width: 250px;
    padding: 0px 0rem .4rem .4rem;
  }
  .speech2textbox{
    min-height: 150px;
  }
  .speech2text{
    font-size: small;
  }
  .lootieanimation{
    width: 50px;
  }

  .copybutton{
    padding: .4rem;
  }
  .miclogo{
    padding: .5rem;
    font-size: 20px;
  }
}